/* the row with years months and days */
#root > div > form > div:nth-child(3) > fieldset > div {
  width: 80%;
}

/* outer flex container with Name, Age, Gender sections */
.flex-container-row {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  width: 100%;
  padding-top: 10px;
}

/* flex items */
.flex-item {
  -ms-flex: 1 1;
      flex: 1 1;
  min-width: 50%;
}

.flex-item2 {
  display: -ms-flexbox;
  display: flex;
  min-width: 100%;
}

.form-input {
  -ms-flex: 1 1;
      flex: 1 1;
}

#estimatedTitle {
  display: inline-block;
  margin: auto 0;
}

.checkbox-container {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: start;
      justify-content: flex-start;
}

#hidden-title,
#display-none {
  opacity: 0;
}

/* Radio buttons flex container */
.radioGroup {
  display: -ms-flexbox;
  display: flex;
  line-height: var(--height);
  width: 50%;
}

.labelRadio {
  text-align: center;
  position: relative;
  -webkit-box-shadow: 0 0 2px 0 rgba(0, 0, 0, 0.12), 0 2px 2px 0 rgba(0, 0, 0, 0.24);
          box-shadow: 0 0 2px 0 rgba(0, 0, 0, 0.12), 0 2px 2px 0 rgba(0, 0, 0, 0.24);
}

.radioButton {
  background-color: rgba(155, 155, 155, 0.05);
  color: #2e3436;
}

:checked + span.radioButton {
  background-color: rgba(15, 113, 101, 0.13);
  color: var(--background-color);
  display: inline-block;
  border: none;
  width: 100%;
}

.submit-button {
  display: -ms-flexbox;
  display: flex;
}

.input-label {
  padding-bottom: 15px;
}

.required:after {
  content: ' *';
  color: red;
}

@media (max-width: 910px) {
  #root > div > form > div:nth-child(3) > fieldset > div {
    width: 80%;
  }
}

@media (max-width: 600px) {
  .flex-item,
  .flex-item2 {
    min-width: 100%;
  }

  #root > div > form > div:nth-child(3) > fieldset > div {
    width: 100%;
  }
}



